import vCard from 'vcf';

function jsonToVCF(data) {
  let vcfString = "BEGIN:VCARD\nVERSION:3.0\n";

  data.forEach(item => {
    switch (item.key) {
      case 'name':
        vcfString += `N:${item.value}\n`;
        break;
      case 'company':
        vcfString += `ORG:${item.value}\n`;
        break;
      case 'designation':
        vcfString += `TITLE:${item.value}\n`;
        break;
      case 'officeNumber':
        const officeNumbers = item.value.replaceAll(' ', '').split(',');
        officeNumbers.forEach(number => {
          vcfString += `TEL;TYPE=work:${number}\n`;
        });
        break;
      case 'faxNumber':
        const faxNumbers = item.value.replaceAll(' ', '').split(',');
        faxNumbers.forEach(number => {
          vcfString += `TEL;TYPE=fax:${number}\n`;
        });
        break;
      case 'mobileNumber':
        const mobileNumbers = item.value.replaceAll(' ', '').split(',');
        mobileNumbers.forEach(number => {
          vcfString += `TEL;TYPE=home:${number}\n`;
        });
        break;
      case 'email':
        vcfString += `EMAIL:${item.value}\n`;
        break;
      // case 'branch':
      //   vcfString += `LABEL:${item.value}\n`;
      //   break;
      case 'officeAddress':
        vcfString += `ADR;TYPE=work;LABEL:${item.value}\n`;
        break;
      default:
        break;
    }
  });
  // vcfString += "REV:20121201T134211Z\n";
  vcfString += "END:VCARD";
  console.log('vcfString', vcfString);
  return vcfString;
}

export function downloadVCard(contactData) {
  const vCardData = jsonToVCF(contactData);
  const element = document.createElement('a');
  const file = new Blob([vCardData], { type: 'text/vcard' });
  element.href = URL.createObjectURL(file);
  element.download = 'contact.vcf';
  document.body.appendChild(element); // Required for this to work in FireFox
  element.click();
}

export function shareVCard(vcfData) {
  if (navigator.share) {
    const vCardData = jsonToVCF(vcfData);
    const vcfBlob = new Blob([vCardData], { type: 'text/vcard' });
    const file = new File([vcfBlob], 'contact.vcf', { type: 'text/vcard' });

    navigator.share({
      title: 'Share Public Bank eBiz Card',
      files: [file],
    })
      .then(() => console.log('Shared successfully'))
      .catch((error) => console.error('Error sharing:', error));
  } else {
    console.error('Web Share API is not supported or invalid vCard data');
    downloadVCard(vcfData)
  }
}