import React, { useEffect, useRef } from 'react';
import cardIcon from './assets/images/card-white.png';
import './App.css';
import { useDispatch, useSelector } from 'react-redux';
import ListHeader from './components/ListHeader';
import ContactInformation from './components/ContactInformation';
import MarketingLists from './components/MarketingLists';
import { eBizData, eBizDataLoading, fetchEbizData } from './store/slices/contactSlice';
import Button from './components/Button';
import ButtonWhite from './components/ButtonWhite';
import ExportIcon from './assets/images/export.png';
import ShareIcon from './assets/images/share-icon.png';
import Card from './components/Card';
import { downloadVCard, shareVCard } from './utils/vcard';

function App() {
  const search = window.location.search;
  const params = new URLSearchParams(search);
  const session = params.get('session');
  const cardRef = useRef(null)
  const cardInfo = useSelector(eBizData);
  const isDataLoading = useSelector(eBizDataLoading);
  useEffect(() => {
    initialize()
  }, []);

  const data = useSelector(eBizData);
  const dispatch = useDispatch<any>();
  const getData = async () => {
    await dispatch(fetchEbizData(session));
  };
  const initialize = async () => {
    if (Object.keys(data).length === 0) {
      await getData();
    }
  };

  console.log('isDataLoading', isDataLoading);
  console.log('cardInfo', cardInfo);

  let expired = session === null || session === undefined || (isDataLoading === false && cardInfo?.length === 0)

  if (isDataLoading === true && cardInfo?.length === 0) {
    return <main className='flex min-h-screen flex-col items-center justify-between bg-[#fff]'>
      <ListHeader title='Public Bank Dynamic eBiz Card Profile' image={cardIcon} containerStyle='flex-1' />
      <div className='w-screen sm:max-w-sm lg:max-w-[898px] shadow-sm lg:shadow-none bg-[#fff] mx-auto '>
        <div className='lg:flex-row lg:justify-around lg:flex'>
          <div className='flex flex-col justify-center lg:items-center mt-[12px]'>
            <div ref={cardRef}>
              <Card expired={expired} loading={isDataLoading} />
            </div>
          </div>
        </div>

        <div className='flex justify-center mt-[10px] py-[22px]'>
          <MarketingLists />
        </div>

      </div>
      <ListHeader title='Copyright  2024 Public Bank Berhad 196501000672 (6463-H). All Rights Reserved.' textStyle='!font-[400] !text-[14px] text-center' />
    </main>
  }

  if (expired) {
    return <main className='flex min-h-screen flex-col items-center justify-between bg-[#fff]'>
      <ListHeader title='Public Bank Dynamic eBiz Card Profile' image={cardIcon} containerStyle='flex-1' />
      <div className='w-screen sm:max-w-sm lg:max-w-[898px] shadow-sm lg:shadow-none bg-[#fff] mx-auto '>
        <div className='lg:flex-row lg:justify-around lg:flex'>
          <div className='flex flex-col justify-center lg:items-center mt-[12px]'>
            <div ref={cardRef}>
              <Card expired={true} />
            </div>
          </div>
        </div>

        <div className='flex justify-center mt-[10px] py-[22px]'>
          <MarketingLists />
        </div>

      </div>
      <ListHeader title='Copyright  2024 Public Bank Berhad 196501000672 (6463-H). All Rights Reserved.' textStyle='!font-[400] !text-[14px] text-center' />
    </main>
  }

  return (
    <main className='flex min-h-screen flex-col items-center justify-between bg-[#fff]'>
      <ListHeader title='Public Bank Dynamic eBiz Card Profile' image={cardIcon} containerStyle='flex-1' />
      <div className='w-screen sm:max-w-sm lg:max-w-[898px] shadow-sm lg:shadow-none bg-[#fff] mx-auto '>
        <div className='lg:flex-row lg:justify-around lg:flex'>
          <div className='flex flex-col justify-center lg:items-center mt-[12px]'>
            <div ref={cardRef}>
              <Card />
            </div>

            <div className='hidden lg:flex lg:flex-col'>
              <Button leftIcon={ExportIcon} title='Save Contact Info to Phone' />
              <ButtonWhite leftIcon={ShareIcon} title='Share Contact to Others' />
            </div>
          </div>

          <div className='flex flex-col justify-center mt-[12px] lg:p-0 lg:pr-[60px] px-[24px]'>
            <ContactInformation />
            <div className='flex flex-col lg:hidden lg:flex-col'>
              <Button leftIcon={ExportIcon} title='Save Contact Info to Phone' onClick={() => downloadVCard(data)} />
              <ButtonWhite leftIcon={ShareIcon} title='Share Contact to Others' onClick={() => shareVCard(data)} />
              <div className='h-[20px]' />
            </div>
          </div>
        </div>

        <div className='flex justify-center mt-[10px] py-[22px]'>
          <MarketingLists />
        </div>

      </div>
      <ListHeader title='Copyright  2024 Public Bank Berhad 196501000672 (6463-H). All Rights Reserved.' textStyle='!font-[400] !text-[14px] text-center' />
    </main>
  );
}

export default App;
