import React from 'react';
import { CardListState } from './components.type';

export default function CardList({ image, title = '', numberOfLines = 2, cardDesign, preText = '', style = {} }: CardListState) {
  const imageStyle = {
    filter: cardDesign?.type === 'type3' || cardDesign?.type === 'type5' || cardDesign?.type === 'type4' ? ` invert(99%) sepia(55%) saturate(0%) hue-rotate(120deg) brightness(192%) contrast(100%)` : `hue-rotate(0deg)`,
    width: '18px',
    height: '18px',
    marginRight: '10px',
  };

  return (
    <div className='flex'>
      {image && <img src={image} alt='PB' style={imageStyle} />}
      <div>
        <p style={{ color: cardDesign?.textColor, ...style }} className={`text-[8.5px] leading-[10.5px]`}>
          {preText?.length > 0 ? preText + ' ' : ''}{title}
        </p>
      </div>
    </div>
  );
}
