import React, { useEffect } from 'react';
import infoIcon from '../assets/images/info.png';
import ContactList from './ContactList';
import { useSelector } from 'react-redux';
import { eBizData } from '../store/slices/contactSlice';

export default function ContactInformation() {
  const data = useSelector(eBizData);
  const vcfData = {};
  useEffect(() => {
    data.map((item) => (vcfData[item.key] = item.value));
  }, [data, vcfData]);

  return (
    <div className='flex flex-col'>
      <div className='flex-1 max-w-sm lg:max-w-screen-2xl bg-white drop-shadow-2xl p-[22px] pb-[10px] rounded-[6px]'>
        <div className='flex items-center'>
          <img src={infoIcon} className='w-[18px] h-[18px] lg:w-[24px] lg:h-[24px] mr-[10px] object-contain' alt='PB' />
          <p className={`text-[12px] text-black font-bold lg:text-[16px] mt-[1px]`}>Contact Information</p>
        </div>
        <div className='mt-4'>
          {data.map((item, index) => (item.key === 'cardType' || item.key === 'url' || item.key === 'profile' ? null : <ContactList key={index} title={item.label} value={item.value} />))}
        </div>
      </div>
    </div>
  );
}
