import React from 'react';
import { ButtonState } from './components.type';

export default function Button({ title = '', onClick = () => { }, style, textStyle, leftIcon }: ButtonState) {
  return (
    <div className='bg-[#C32C2C] lg:w-[394px] mt-[24px] flex items-center py-[12px] rounded-[6px] cursor-pointer hover:drop-shadow-md transition ease-in' style={style} onClick={onClick}>
      {leftIcon && <img src={leftIcon} className='w-[18px] h-[18px] mx-[14px]' alt='PB' />}
      <p className='text-white font-bold text-[13px]' style={textStyle}>
        {title}
      </p>
    </div>
  );
}
