import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';

import { RootState } from '../../store';
import { EBizCardData } from '../../mockData/eBizCardData';
import { handleThunkResponse } from '../../utils/utils';
import { API, isDemo } from '../../constants/constants';
import axios from 'axios';
import client from '../../utils/client';

export interface ContactObjectState {
  name?: string;
  company?: string;
  title?: string;
  officeNumber?: string;
  mobileNumber?: string;
  email?: string;
  officeAddress?: string;
  notes?: string;
  designation?: string;
  branch?: string;
}
export interface DataObjectState {
  key?: string;
  label?: string;
  value?: string;
}
export interface ContactState {
  ebizData: DataObjectState[];
  loading: boolean;
}

export const fetchEbizData = createAsyncThunk(
  'fetchEbizData',
  async (request: string, { getState, rejectWithValue }) => {
    if (isDemo) {
      return EBizCardData.cardData;
    } else if (request) {
      // const result = await axios.get(API + `/EBiz/data/web/?sessionId=${request}`);
      const result = await axios.get(API + `/EBiz/data/web/?sessionId=${request}`);
      console.log('res1', result);
      return result
    } else {
      return EBizCardData.cardData;
    }
  }

);
const initialState: ContactState = {
  ebizData: [],
  loading: false
};

export const ContactSlice = createSlice({
  name: 'contact',
  initialState,
  reducers: {
  },
  extraReducers: (builder) => {
    builder.addCase(fetchEbizData.rejected, (state, { payload }) => {
      state.loading = false;
      state.ebizData = []
    });
    builder.addCase(fetchEbizData.pending, (state, { payload }) => {
      state.loading = true;
    });
    builder.addCase(fetchEbizData.fulfilled, (state, { payload }) => {
      state.loading = false;
      state.ebizData = payload.data;
    });
  },
});

// export const {} = ContactSlice.actions;

export const eBizData = (state: RootState) => state.contact.ebizData;
export const eBizDataLoading = (state: RootState) => state.contact.loading;

export default ContactSlice.reducer;
